<template>
  <div class="bond-pay">
    <back-header title="缴纳保证金" />
    <div class="money">
      <h4>需支付金额(元）</h4>
      <p>¥1000</p>
    </div>
    <pay-select v-model="payType"/>
    <div class="pay-btn" @click="pay">立即支付</div>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import PaySelect from '../../components/pay-select/pay-select'

export default {
  name: 'bond-pay',
  components: {
    BackHeader,
    PaySelect
  },
  data() {
    return {
      payType: 1
    }
  },
  methods: {
    pay() {
      window.payCallback = (status) => {
        switch (status) {
          case 'SUCCESS':
            this.$router.goBack()
            this.$router.goBack()
            break
          case 'FAIL':
            this.$toast.fail('支付失败')
            break
          case 'CANCEL':
            this.$toast.fail('支付取消')
            break
        }
      }
      this.$jsCallNative('pay', {
        order_no: '',
        total_fee: process.env.NODE_ENV === 'development' ? 0.01 : 1000,
        pay_channel: this.payType
      })
    }
  }
}
</script>

